import React, { useState } from 'react';
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import ReactDOM from 'react-dom/client';



window.rawWindow ? window.rawWindow.wm2 = __webpack_modules__
: window.wm2 = __webpack_modules__;






function MyComponent() {
  const [value, setValue] = useState('');

  function click() {
    var odiv = document.createElement('div');
    odiv.innerHTML = 'appended child';
    document.body.appendChild(odiv)
    
  }
  return (
    <div>
      <button onClick={click}>body append child</button>
      <ReactQuill theme="snow" value={value} onChange={setValue} />
    </div>
  );
}

export default MyComponent;

export function mount(dom) {
  ReactDOM.createRoot(dom).render(<MyComponent />);
}
